<template>
	<view class="container">
		<uni-list>
			<uni-list-item v-for="(item,index) in Data" :key="index" :title="item.title" note="Dcloud 128评论 21 赞">
				       <template v-slot:header>
				            <view class="uni-thumb">
								<image class="uni-media-list-logo" :src="item.author_avatar" style="width: 120upx;height: 120upx;"></image>
								</view>
				        </template>
			</uni-list-item>
		</uni-list>
		<view class="page-section page-section-gap">
		<map :latitude="latitude" :longitude="longitude" style="width: 100%;height: 600upx;"></map>
						</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				Data: [],
				 latitude: 39.7,
				longitude: 116.7,
			}
		},
		onLoad() {
			uni.showLoading({
							title:"加载中...."
						})
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news',
				method: 'GET',
				data: {},
				success: res => {
					this.Data = res.data;
					uni.hideLoading();
				},
				fail: () => {},
				complete: () => {}
			});
			uni.getLocation({
				type: 'gcj02',
				success: res => {
					console.log(12345)
					console.log(res)
				},
				fail: () => {
					console.log(44444)
					uni.showToast({
										    title: '获取地址失败，将导致部分功能不可用',
											icon:'none'
										});
				},
				complete: () => {
					console.log(5555)
				}
			});
		},
		methods: {

		}
	}
</script>

<style>
	.container {
		padding: 20px;
		font-size: 14px;
		line-height: 24px;
	}
</style>
